<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>港口管理</BreadcrumbItem>
      <BreadcrumbItem>港口详情</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>港口基本信息</span>
          <Row slot="content" class="row">
              <Col span="3">港口</Col>
              <Col span="6">{{result.port ? result.port:"-"}}</Col>
              <Col span="3">港口使用费</Col>
              <Col span="6">{{result.usageFee ? result.usageFee:"-"}}</Col>
          </Row>
          <Row slot="content" class="row">
              <Col span="3">吃水</Col>
              <Col span="6">{{result.shipDraught ? result.shipDraught:"-"}}</Col>
              <Col span="3">港口作业区</Col>
              <Col span="6">{{result.polygnIndex ? result.polygnIndex:"-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">船长限制</Col>
              <Col span="6">{{result.shipLength ? result.shipLength:"-"}}</Col>
              <Col span="3">船宽限制</Col>
              <Col span="6">{{result.shipBreadth ? result.shipBreadth:"-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">封航状态</Col>
              <Col span="6">{{result.stopStatus == 0 ? '正常':'停止作业'}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">封航/停止作业开始时间</Col>
              <Col span="6">{{result.startDate ? result.startDate:"-"}}</Col>
              <Col span="3">封航/开始作业结束时间</Col>
              <Col span="6">{{result.endDate ? result.endDate:"-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">封航原因</Col>
              <Col span="6">{{result.stopReason ? result.stopReason:"-"}}</Col>
          </Row>

          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
              <Button type="primary">
                <router-link :to="{
                 path: '/portCenter/updateport',query:{
                   code:this.$route.query.code
                }
              }" style="color:#fff">修改港口信息</router-link>
                </Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:''
            }
        },
         methods:{
          async  portDetail(){
              let data  = {
                portCode:this.$route.query.code
              }
              try{
                  let res = await this.$http.portDetail(data)
                  if(0==res.data.status ){
                    this.result = res.data.result
                  }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
           }
        },
        created(){
          this.portDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
}

</style>



